@use "../../styles/variables" as *;

@media screen and (min-width: 768px) {
    .container__grid {

        display: grid;
        grid-template-columns:  1fr 1fr 1fr;
        grid-gap: 10px;
        gap: 10px;
        padding: 10px;
        margin: 20px;
        justify-content: center;
        align-items: center;
        font-family: $font-primary;
  }
}